import React from 'react';

import ParticleField from 'react-particles-webgl';

export default function LandingBackground() {
  const config = {
    showCube: false,
    dimension: '2D',
    velocity: 0.1,
    boundaryType: 'bounce',
    antialias: true,
    direction: {
      xMin: -1,
      xMax: 1,
      yMin: -1,
      yMax: 1,
      zMin: -1,
      zMax: 1,
    },
    lines: {
      colorMode: 'solid',
      color: '#666666',
      transparency: 0.9,
      limitConnections: true,
      maxConnections: 50,
      minDistance: 100,
      visible: true,
    },
    particles: {
      colorMode: 'solid',
      color: '#666666',
      transparency: 0.9,
      shape: 'circle',
      boundingBox: 'canvas',
      count: window.outerWidth > 768 ? 75 : 25,
      minSize: 20,
      maxSize: 50,
      visible: true,
    },
    cameraControls: {
      enabled: false,
      enableDamping: true,
      dampingFactor: 0.9,
      enableZoom: true,
      autoRotate: false,
      autoRotateSpeed: 0.3,
      resetCameraFlag: true,
    },
  };
  return (
    <div style={{ position: 'absolute', top: 0, left: 0 }} className="w-100 h-100">
      <ParticleField config={config} />
    </div>
  );
}
